import React, { useState, useRef, useEffect, useCallback } from "react";
import { useSpring, animated } from "react-spring";
import {
  StyledForm,
  StyledInput,
  StyledError,
  StyledButton,
  StyledFieldset,
} from "../Reservation/Reservation.elements";
import { Background, ModalWrapper, CloseModalButton } from "./EditFormElements";
import { useStore, updateReservation } from "../../services/reservations";

export const EditForm = ({ showModal, setShowModal, selectedRow, reservations, open, setOpen, response, setResponse}) => {

  const reservationToEdit = reservations.filter(resv => resv.id === selectedRow[0])[0]

  //set initial state in form values to the original dataset reservation to be updated.
  let initialState = {
    id: reservationToEdit ? reservationToEdit.id : "",
    name: reservationToEdit ? reservationToEdit.name : "",
    email: reservationToEdit ? reservationToEdit.email : "",
    phone: reservationToEdit ? reservationToEdit.phone : "",
    pax: reservationToEdit ? reservationToEdit.pax : "",
    date: reservationToEdit ? reservationToEdit.date : "",
    time: reservationToEdit ? reservationToEdit.time : "",
  };


  const timeSlots = [
    "12:00",
    "13:00",
    "14:00",
    "15:00",
    "16:00",
    "17:00",
    "18:00",
    "19:00",
    "20:00",
  ];

  const [state, setState] = useState(initialState);
  const [error, setError] = useState("");
  const editReservation = useStore((state) => state.editReservation);

  const modalRef = useRef();

  const animation = useSpring({
    config: {
      duration: 250,
    },
    opacity: showModal ? 1 : 0,
    transform: showModal ? `translateX(0%)` : `translateX(-100%)`,
  });

  const closeModal = (e) => {
    if (modalRef.current === e.target) {
      setShowModal(false);
    }
  };

  const keyPress = useCallback(
    (e) => {
      if (e.key === "Escape" && showModal) {
        setShowModal(false);
        console.log("I pressed");
      }
    },
    [setShowModal, showModal]
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    for (let key in state) {
      if (state[key] === "") {
        setError(`*You must provide the ${key} details.`);
        return;
      }
    }
    setError("*We have updated your reservation!");

    // creates a put request to mongodb to store the new reservation.
    updateReservation(selectedRow, state);
    // edits the reservation in the store (local storage).
    editReservation(selectedRow[0], state);
    console.log(reservations);

    //close modal and show response message
    setShowModal(!showModal)
    setResponse("Success: we have updated your reservation!")
    setOpen(!open)
  };

  const handleInput = (e) => {
    const inputField = e.currentTarget.name;
    const value = e.currentTarget.value;

    setState((prev) => ({ ...prev, [inputField]: value }));
  };

  useEffect(() => {
    document.addEventListener("keydown", keyPress);
    return () => document.removeEventListener("keydown", keyPress);
  }, [keyPress]);

  return (
    <>
      {showModal ? (
        <Background onClick={closeModal} ref={modalRef}>
          <animated.div style={animation}>
            <ModalWrapper showModal={showModal}>
              <StyledForm onSubmit={handleSubmit}>
                <CloseModalButton
                  aria-label="Close modal"
                  onClick={() => setShowModal((prev) => !prev)}
                />
                <StyledInput
                  type="text"
                  name="ID"
                  maxLength="30"
                  value={"ID: " + state.id}
                  disabled
                />
                <StyledInput
                  type="text"
                  placeholder="Name"
                  name="name"
                  maxLength="30"
                  value={state.name}
                  onChange={handleInput}
                />
                <StyledInput
                  type="email"
                  placeholder="Email"
                  name="email"
                  maxLength="30"
                  value={state.email}
                  onChange={handleInput}
                />
                <StyledInput
                  type="text"
                  placeholder="Phone"
                  name="phone"
                  maxLength="8"
                  value={state.phone}
                  onChange={handleInput}
                />
                <StyledInput
                  type="number"
                  placeholder="Pax"
                  name="pax"
                  min="0"
                  max="5"
                  value={state.pax}
                  onChange={handleInput}
                />
                <StyledInput
                  type="date"
                  placeholder="Date"
                  name="date"
                  min="2021-07-12"
                  value={state.date}
                  onChange={handleInput}
                />
                <StyledFieldset>
                  {timeSlots.map((slot, index) => {
                    return (
                      <label key={index}>
                        <input
                          type="radio"
                          value={slot}
                          name="time"
                          checked={state.time === slot}
                          onChange={handleInput}
                        />
                        {slot}
                      </label>
                    );
                  })}
                </StyledFieldset>
                {error && (
                  <StyledError>
                    <p>{error}</p>
                  </StyledError>
                )}
                <StyledButton type="submit">Update</StyledButton>
              </StyledForm>
            </ModalWrapper>
          </animated.div>
        </Background>
      ) : null}
    </>
  );
};
