import { useEffect } from 'react'
import { useLocation } from 'react-router-dom'

/*
function that scrolls page to the top, used when clicking a button.
*/

export default function ScrollToTop() {
    const { pathname } = useLocation()
    useEffect(() => {
        window.scrollTo(0, 0);
    }, 
    [pathname]
    )

    return null
}